<script>
import VMdPreview from "../plugins/markdown-preview";
export default {
  props: ["id"],
  data() {
    return {
      articleInfo: {},
      total: 0,
      queryParam: {
        pagesize: 5,
        pagenum: 1,
      },
    };
  },
  created() {
    this.getArticleInfo();
  },
  methods: {
    // 查询文章
    async getArticleInfo() {
      const { data: res } = await this.$http.get(`article/info/${this.id}`);
      this.articleInfo = res.data;
    },
  },
};
</script>
